.departments-section {
  background-color: $color_light_pink;
  position: relative;
}

.departments-main-header {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.departments-container {
  margin-left: auto;
  margin-right: auto;
  margin-top: -46px;
}

.departments-list {
  list-style: none;
  margin: 0;
  padding: 0;
  line-height: 0;
  @media screen and (min-width: 768px) {
    display: flex;
  }
  @media screen and (min-width: 1280px) {
    margin-right: -31px;
    margin-right: 115px;
    margin-left: 115px;
  }
}

.departments-listik {
  width: 220px;
  background: $color_white;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
  margin-left: auto;
  margin-right: auto;
  &:not(:last-child) {
    margin-bottom: 20px;
  }
  @media screen and (min-width: 480px) {
    width: 70vw;
  }
  @media screen and (min-width: 768px) {
    width: calc((100% - 20px * 2) / 3);

    &:not(:last-child) {
      margin-right: 20px;
      margin-bottom: 0px;
    }
  }
  @media screen and (min-width: 1280px) {
    width: calc((100% - 31px * 2) / 3);

    &:not(:last-child) {
      margin-right: 31px;
    }
  }
}

.departments-thumb {
  padding: 43px 20px 43px 21px;
  @media screen and (min-width: 493px) {
    text-align: center;
  }
  @media screen and (min-width: 768px) {
    text-align: left;
  }
}

.departments-header {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.37;
  color: $color_white;
  text-align: center;
}

.cafe {
  display: block;
  background-color: $color_yellow;
  border-radius: 4px;
  width: 80px;
  height: 16px;
  @media screen and (min-width: 493px) {
    margin-left: auto;
    margin-right: auto;
  }
  @media screen and (min-width: 768px) {
    margin-left: 0;
    margin-right: 0;
  }
}

.FOODTRUCK {
  width: 130px;
  height: 16px;
  background: $color_green;
  border-radius: 4px;
  display: block;
  @media screen and (min-width: 493px) {
    margin-left: auto;
    margin-right: auto;
  }
  @media screen and (min-width: 768px) {
    margin-left: 0;
    margin-right: 0;
  }
}

.departments-miniheader {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.37;
  letter-spacing: 0.04em;
  color: $color_black;
  margin-top: 25px;
  margin-bottom: 0;
}

.departments-description {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.5;
  color: $color_brown;
  margin-top: 10px;
  display: inline-block;
}

.departments-workingtime {
  border-top: 1px solid #e1e1e1;
  border-bottom: 1px solid #e1e1e1;
  margin-top: 25px;
  margin-bottom: 25px;
  & ul {
    margin: 0;
    padding: 0;
    padding-top: 25px;
    padding-bottom: 25px;

    @media screen and (min-width: 493px) {
      display: flex;
    }
    @media screen and (min-width: 768px) {
      display: inline-block;
    }
  }
}

.hours-list {
  list-style: none;
  margin: 0;
  padding: 0;

  &:nth-child(2) {
    padding-top: 10px;
  }
  @media screen and (min-width: 493px) {
    &:nth-child(2) {
      padding-top: 0;
      margin-left: auto;
    }

    @media screen and (min-width: 768px) {
      &:nth-child(2) {
        padding-top: 10px;
      }
    }
  }
}

.departments-week {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.37;
  color: $color_black;
  display: block;
  margin: 0;
}

.departments-hours {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.83;
  color: $color_secondary_pink;
  display: block;
}

.departmnets-contacts {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.5;
  color: $color_brown;
  text-decoration: none;
}

.departments-button-bright {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 35px;
  padding-right: 35px;
  border-radius: 22px;
  font-size: 16px;
  border: none;
  width: 198px;
  height: 44px;
  background-color: $color_red;
  color: $color_white;
  transition: background-color 250ms $cubic;
  margin-left: auto;
  margin-right: auto;
  cursor: pointer;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.3;
  text-decoration: none;

  &:hover,
  &:focus {
    background-color: $color_secondary_pink;
  }
  @media screen and (min-width: 768px) {
    margin-top: 0;
    margin-left: 0;
    margin-right: 0;
  }
}

.departments-btns {
  padding-top: 44px;
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: 768px) {
    padding-top: 64px;
  }
  & a {
    text-decoration: none;
    @media screen and (min-width: 768px) {
      display: flex;
      justify-content: center;
    }
  }
}

.department-icon-white {
  width: 3.61px;
  height: 8px;
  fill: $color_white;
}

.departments-button-pale {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 50px;
  padding-right: 50px;
  border-radius: 22px;
  font-size: 16px;
  border: none;
  width: 198px;
  height: 44px;
  background-color: $color_white;
  color: $color_red;
  margin-left: auto;
  margin-right: auto;
  cursor: pointer;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.3;
  transition: background-color 250ms $cubic;
  margin-top: 10px;

  &:hover,
  &:focus {
    background-color: $color_secondary_pink;
    color: $color_white;
  }

  @media screen and (min-width: 768px) {
    margin-top: 0;
    margin-left: 15px;
    margin-right: 0;
  }
  @media screen and (min-width: 1280px) {
    margin-left: 17px;
  }
}
